import React from 'react';

import './slideF.less';

export default function SlideF(props) {
  const {index, currentIndex} = props,
      {clientHeight} = document.body;
  return (
		<div className="slide-item slide-f"  style={{'height': clientHeight}}>
			<img className="background-f" src={require("../../assets/slide-f/background.png")} alt=""/>
			<img
           className={`fish-f ${index === currentIndex ? 'fade-in-up' : ''}`}
           src={require("../../assets/slide-f/fish.png")} alt=""/>
		</div>
	);
}



