import React from 'react';
import ReactDOM from 'react-dom';
import {HashRouter, Route, Switch} from 'react-router-dom';

import Slides from './pages/slides/Slides';

import SlideA from './components/SlideA/SlideA';

import SlideH from './components/SlideH/SlideH';

import registerServiceWorker from './registerServiceWorker';

import './index.less'

ReactDOM.render(
    <HashRouter>
        <Switch>
            <Route exact path='/' component={SlideA}></Route>
            <Route path='/slides' component={Slides}></Route>
            <Route path='/save' component={SlideH}></Route>
        </Switch>
    </HashRouter>
    ,
    document.getElementById('root')
);
registerServiceWorker();



