import React, {Component} from 'react';
import Slider from "react-slick";
import './slides.less';

import SlideB from '../../components/SlideB/SlideB';
import SlideC from '../../components/SlideC';
import SlideD from '../../components/SlideD/SlideD';
import SlideE from '../../components/SlideE';
import SlideF from '../../components/SlideF/SlideF';
import SlideG from '../../components/SlideG';

export default class Slides extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentIndex: 0,
    }
  }

  componentDidMount() {
  }

  render() {
    const self = this;
    const option = {
      dots: false,
      infinite: false,
      vertical: true,
      verticalSwiping: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      afterChange: function (index) {
        self.setState({
          currentIndex: index,
        });
      },
    };
    const currentIndex = this.state.currentIndex;
    return (
      <div className="page-wrapper">
        <Slider {...option}>
          <SlideB index={0} currentIndex={currentIndex}/>
          <SlideC index={1} currentIndex={currentIndex}/>
          <SlideD index={2} currentIndex={currentIndex}/>
          <SlideE index={3} currentIndex={currentIndex}/>
          <SlideF index={4} currentIndex={currentIndex}/>
          <SlideG index={5} currentIndex={currentIndex}/>
        </Slider>
      </div>
    )
  }
}






