import React from 'react';

import './slideD.less';

export default function SlideD(props) {
  const {index, currentIndex} = props,
      {clientHeight} = document.body;
  return (
    <div className="slide-item"  style={{'height': clientHeight}}>
      <img className="background-d" src={require("../../assets/slide-d/background.png")} alt=""/>
      <img
        className={`fish-d ${index === currentIndex ? 'fade-in' : ''}`}
        src={require("../../assets/slide-d/fish.png")} alt=""/>
    </div>
  );
}



