import React from 'react';

import html2canvas from 'html2canvas';


import './slideH.less';

export default class SlideH extends React.Component {

	constructor(props){
		super(props);
		this.state = {
			imgNum : this.getRandomNumber(),
            imgSrc: '',
            showImg: 'hide'
        }
	}

	refresh = () => {
		const random = this.getRandomNumber();
        console.log(random);
        this.setState({
            imgNum : random
		})
	};

	getRandomNumber = () => {
		return parseInt(Math.random()* 9, 10) || 1;
	}

    saveScreenShot = () => {
    	const self = this;
        html2canvas(document.querySelector('#print-zone')).then(function(canvas) {
            document.body.appendChild(canvas);
            const base64Url = canvas.toDataURL('image/png');
            self.setState({
                imgSrc: base64Url,
                showImg: 'show'
            })

        });
	};

	generateName = () => {

		const nameList = localStorage.getItem("name").split('')|| ['','',''];

		return nameList.map((i, index)=>{
			return <p key={index}>{i}</p>
        })
    }

	render(){
        return (
			<div className="slide-item slide-h" >
				<img className={`output-img ${this.state.showImg}`} src={this.state.imgSrc} />
				<div className={`customized-name color${this.state.imgNum}`}>{this.generateName()}</div>
				<div id="print-zone">
					<img className="background-h" src={require(`../../assets/slide-h/Group-${this.state.imgNum}.png`)} alt=""/>
					<div className="qrcode-wrapper">
						<img className="qrcode" src={require('../../assets/slide-h/qrcode.png')}/>
						<p></p>
						<p></p>
					</div>
				</div>
				<div className="button-wrapper-h">
					<img className="button-refresh" src={require("../../assets/slide-h/left-button.png")}  onClick={this.refresh}/>
					<img className="button-save" src={require("../../assets/slide-h/right-button.png")} onClick={this.saveScreenShot} />
				</div>
			</div>
        );
	}

}



