import React from 'react';

import './index.less';

export default function SlideB(props) {
  const {index, currentIndex} = props,
      {clientHeight} = document.body;

  return (
    <div className="slide-item"  style={{'height': clientHeight}}>
      <img className="background" src={require("../../assets/slide-e/bg.png")} alt=""/>
      <img className="e-tree" src={require("../../assets/slide-e/tree.png")} alt=""/>
      <img className="e-text" src={require("../../assets/slide-e/text.png")} alt=""/>
      <img
        className={`e-fish ${index === currentIndex ? 'fade-in-up' : ''}`}
        src={require("../../assets/slide-e/fish.png")} alt=""/>
    </div>
  );
}



