import React from 'react';
import './slideA.less';


export default function SlideA() {
  return (
    <div className="wrapper-a">
      <div className="background-wrapper">
        <img src={require('../../assets/slide-a/background.png')}/>
      </div>
      <img className="top-text" src={require("../../assets/slide-a/text-top.png")}/>
      <div className="wave-wrapper fade-in-up">
        <img className="left-wave" src={require("../../assets/slide-a/left-wave.png")} alt=""/>
        <img className="right-wave" src={require("../../assets/slide-a/right-wave.png")} alt=""/>
        <img className="wave1" src={require("../../assets/slide-a/wave1.png")} alt=""/>
        <img className="wave2" src={require("../../assets/slide-a/wave2.png")} alt=""/>
      </div>
      <div className="red-packet fade-in">
        <img className="red-packet-1" src={require('../../assets/slide-a/rp-1.png')} alt=""/>
        <img className="red-packet-2" src={require('../../assets/slide-a/rp-2.png')} alt=""/>
        <img className="red-packet-3" src={require('../../assets/slide-a/rp-3.png')} alt=""/>
        <img className="red-packet-4" src={require('../../assets/slide-a/rp-4.png')} alt=""/>
      </div>
      <img className="man-fish" src={require("../../assets/slide-a/man-fish.png")}/>
      <img className="btm-wave" src={require("../../assets/slide-a/front-wave.png")} alt=""/>
      <a href="/#/slides">
        <img className="submit-btn scale-up" src={require("../../assets/slide-a/button.png")} alt=""/>
      </a>
    </div>
  );
}



