import React from 'react';

import './slideB.less';

export default function SlideB(props) {
  const {index, currentIndex} = props;
  const {clientHeight} = document.body;
  return (
    <div className="slide-item" style={{'height': clientHeight}}>
      <img className="background-b" src={require("../../assets/slide-b/background.png")} alt=""/>
      <img className={`fish-b ${index === currentIndex ? 'fade-in' : ''}`}
           src={require("../../assets/slide-b/fish.png")} alt=""/>
    </div>
  );
}



