import React from 'react';

import './index.less';

export default function SlideB(props) {
  const {index, currentIndex} = props,
   {clientHeight} = document.body;
    return (
    <div className="slide-item"  style={{'height': clientHeight}}>
      <img className="background" src={require("../../assets/slide-c/bg.png")} alt=""/>
      <img
           className={`fish-c  ${index === currentIndex ? 'fade-in-up' : ''}`}
           src={require("../../assets/slide-c/fish.png")} alt=""/>
      <img className={`c-flower-left ${index === currentIndex ? 'flow-left' : ''}`}
           src={require("../../assets/slide-c/flower-left.png")} alt=""/>
      <img className={`c-flower-right  ${index === currentIndex ? 'flow-right' : ''}`}
           src={require("../../assets/slide-c/flower-right.png")} alt=""/>
    </div>
  );
}



