import React, {Component} from 'react';

import './index.less';

export default class Slides extends Component {
  constructor(props) {
    super(props);
    this.handleNameChange = this.handleNameChange.bind(this);
    this.state = {
      name: '',
      isFocus: false
    }
  }

  handleNameChange(e) {
    let name = e.currentTarget.value;
    if (name.length > 10) {
      name = name.substr(0, 10);
    }
    this.setState({
      name,
    });
  }

  goNext = () => {
    if (this.state.name) {
      localStorage.setItem('name', this.state.name);
      window.location.href = '/#/save';
    }
  }
  handleFocusBlur = (isFocus) => {
    this.setState({
      isFocus,
    });
  }


  render() {
    const {clientHeight} = document.body;
    return (
      <div className="slide-item"  style={{'height': clientHeight}}>
        <img className="background" src={require("../../assets/slide-g/bg.png")} alt=""/>
        <div className="content-wrapper">
          <img className="tip" src={require("../../assets/slide-g/text.png")} alt=""/>
          <div className="input-wrapper">
            {(this.state.name || this.state.isFocus) ? null : <span className="placeholder-tip"></span>}
            <input type="text" className="input-get-name"
                   onChange={this.handleNameChange}
                   onFocus={this.handleFocusBlur.bind(this, true)}
                   onBlur={this.handleFocusBlur.bind(this, false)}
                   value={this.state.name}/>
          </div>
          <img src={require("../../assets/slide-g/btn.png")} alt="" className="submit-btn" onClick={this.goNext}/>
        </div>
      </div>
    );
  }
}



